* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body, ul, li, a {
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  text-decoration: none;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  position: relative;
}

li {
  margin-right: 20px;
}

.header__link img {
  padding-top: 5px;
}

.nav__list {
  align-items: center;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #070707;
  text-transform: uppercase;
  padding: 23px 0;
  font-size: 12px;
  text-decoration: none;
  transition: color .3s;
  display: block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acd7;
}

.is-active:after {
  content: "";
  background: #00acd7;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  transition: background-color .3s;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}
